import React, {Component} from 'react';
import {connect} from 'react-redux';
import {baseUrl, fetchRegister, fetchVideos} from '../../redux/lince/videoActions';
import LinceVideoManager from './LinceVideoManager';
import PropTypes from "prop-types";
import {Col, Row} from 'antd';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import keyboardConfig from "../../config/keyboard.config";
import {LinceVideoPlayer, UrlType} from "lince-player";
import "lince-player/dist/style.css"
//inherited from lince-player
import "videojs-markers/dist/videojs.markers.css"
import regexConfig from "../../config/regex.config";

const LinceValues = {
    NORMAL_SPEED: 1,
    SLOW_SPEED: 0.5,
    STEP: 5,
    STEP_MICRO: 0.1,
    SIZE_FULL: 24,
    SIZE_MEDIUM: 12,
    SIZE_SMALL: 8,
}

class LinceVideoPlayerList extends Component {

    constructor(props) {
        super(props);
        this.state = {mute: true, size: this.props.defaultSize};
        this._doVideoSync = this._doVideoSync.bind(this);
        this._doPlay = this._doPlay.bind(this);
        this._doPause = this._doPause.bind(this);
        this._doMute = this._doMute.bind(this);
        this._doPlayback = this._doPlayback.bind(this);
        this._doResize = this._doResize.bind(this);
        this._doStop = this._doStop.bind(this);
        this.getCurrentTime = this.getCurrentTime.bind(this);
        this.getVideoLength = this.getVideoLength.bind(this);
    }

    componentDidMount() {
        this.props.fetchVideos();
        this.props.fetchRegister();
        //update with marker props (weird behavior, but it needs for second render)
        this._applyAllVideos((v, player) => {
            const vjsPlayer = player.getFullPlayer()
            if (vjsPlayer && vjsPlayer.markers) {
                vjsPlayer.markers({
                    ...vjsPlayer.markers,
                    markerStyle: {
                        'width': '8px',
                        'background-color': 'red'
                    },
                    // markers: this.props.markers
                });
            }
        });
    }

    doPlayScene(time) {
        this._applyAllVideos((v, player) => {
            player.pause();
            player.setTime(time);
        });
    }

    getCurrentTime() {
        let time = 0;
        try {
            if (this.props.videos && this.props.videos.length > 0) {
                const firstPlayer = this.props.videos[0].ref.current;
                if (firstPlayer) {
                    return firstPlayer.getCurrentTime();
                }
            }
        } catch (e) {
            console.log(e)
        }
        return time;
    }

    getVideoLength() {
        let time = 0;
        try {
            if (this.props.videos && this.props.videos.length > 0) {
                const firstPlayer = this.props.videos[0].ref.current;
                if (firstPlayer) {
                    return firstPlayer.getFullPlayer().duration();
                }
            }
        } catch (e) {
            console.log(e)
        }
        return time;
    }

    _applyAllVideos(f) {
        this.props.videos.map((v, player) => f(v, v.ref.current));
    }

    _doVideoSync() {
        let isFirst = true;
        let currentTime;
        this._applyAllVideos((v, player) => {
            if (isFirst) {
                isFirst = !isFirst;
                currentTime = player.getCurrentTime();
            } else {
                player.setTime(currentTime);
            }
        });
    }

    _doPlay() {
        this._applyAllVideos((v, player) => {
            player.play();
        });
    }

    _doPause() {
        this._applyAllVideos((v, player) => {
            player.pause();
        });
    }

    _doStop() {
        this._applyAllVideos((v, player) => {
            player.stop();
        });
    }

    _doMute() {
        this.setState({mute: !this.state.mute});
        this._applyAllVideos((v, player) => {
            // player.muted = this.state.mute;
            player.toggleMute()
        });
    }

    _doPlayback(speed) {
        this._applyAllVideos((v, player) => {
            // player.playbackRate = speed;
            player.playbackRate(speed)
        });
    }

    _doResize(size) {
        this.setState({size: size});
    }

    _renderVideo(key, url, type, ref) {
        const size = this.state.size;
        const isYoutubeLink = regexConfig.isYoutubeVideo(url);
        const videoUrl = (isYoutubeLink) ? url : baseUrl + url;
        const videoType = (isYoutubeLink) ? UrlType.YoutubeUrl : null;
        // type === UrlType.YoutubeUrl comes wrong
        // console.log(key+":"+videoType + "-" + videoUrl)
        return (
            <Col key={key} span={size}>
                <div style={{width: "100%"}}>
                    {/*CRAZY SHIT HERE! It needs a copy of the state to have different scope and be not deleted on updates*/}
                    <LinceVideoPlayer ref={ref} url={videoUrl} style={{width: "100%"}}
                                      type={videoType}
                                      markers={this.props.markers ? [...this.props.markers] : null}/>
                </div>
            </Col>
        )
    }

    _keyboardActions(key, e) {
        switch (key) {
            case keyboardConfig.playKey:
                this._doPlay();
                break;
            case keyboardConfig.pauseKey:
                this._doPause();
                break;
            case keyboardConfig.stopKey:
                this._doStop();
                break;
            case keyboardConfig.videoResizeFullKey:
                this._doResize(LinceValues.SIZE_FULL);
                break;
            case keyboardConfig.videoResizeHalfKey:
                this._doResize(LinceValues.SIZE_MEDIUM);
                break;
            case keyboardConfig.videoResizeThirdKey:
                this._doResize(LinceValues.SIZE_SMALL);
                break;
            case keyboardConfig.normalSpeedKey:
                this._doPlayback(LinceValues.NORMAL_SPEED);
                break;
            case keyboardConfig.halfSpeedKey:
                this._doPlayback(LinceValues.SLOW_SPEED);
                break;
            case keyboardConfig.jumpBackKey:
                this.doPlayScene(this.getCurrentTime() - LinceValues.STEP);
                break;
            case keyboardConfig.jumpForwardKey:
                this.doPlayScene(this.getCurrentTime() + LinceValues.STEP);
                break;
            case keyboardConfig.leftKey:
                this.doPlayScene(this.getCurrentTime() - LinceValues.STEP_MICRO);
                break;
            case keyboardConfig.rightKey:
                this.doPlayScene(this.getCurrentTime() + LinceValues.STEP_MICRO);
                break;
            default:
                break;
        }
        //console.log(`do something upon keydown event of ${key}`);
        return false;
    }

    render() {
        const videos = this.props.videos;
        return (
            <>
                <KeyboardEventHandler
                    handleKeys={this.props.keyboard}
                    onKeyEvent={(key, e) => this._keyboardActions(key, e)}/>
                <Row>
                    <Col span={24}>
                        <LinceVideoManager
                            onSoundTo={this._doMute}
                            onSync={this._doVideoSync}
                            onSetPlayBack={this._doPlayback}
                            onStop={this._doStop}
                            onPause={this._doPause}
                            onPlay={this._doPlay}
                            onResize={this._doResize}
                        />
                    </Col>
                </Row>
                <Row>
                    {videos.map((v) => this._renderVideo(v.id, v.url, v.type, v.ref))}
                </Row>
            </>
        );
    }
}


LinceVideoPlayerList.defaultProps = {
    autoPlay: false,
    defaultSize: LinceValues.SIZE_MEDIUM,
    keyboard: [
        keyboardConfig.playKey,
        keyboardConfig.pauseKey,
        keyboardConfig.stopKey,
        keyboardConfig.videoResizeHalfKey,
        keyboardConfig.videoResizeFullKey,
        keyboardConfig.videoResizeThirdKey,
        keyboardConfig.normalSpeedKey,
        keyboardConfig.halfSpeedKey,
        keyboardConfig.jumpBackKey,
        keyboardConfig.jumpForwardKey,
        keyboardConfig.leftKey,
        keyboardConfig.rightKey,
    ]
};
LinceVideoPlayerList.propTypes = {
    name: PropTypes.string,
    autoPlay: PropTypes.bool,
    defaultSize: PropTypes.number,
    keyboard: PropTypes.array.isRequired,
    videos: PropTypes.array.isRequired,
    markers: PropTypes.array.isRequired,
    register: PropTypes.array.isRequired,
    fetchVideos: PropTypes.func.isRequired,
    fetchRegister: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
    videos: state.lince.videos,
    register: state.lince.register,
    markers: state.lince.register.map(item => {
        return {time: item.videoTime, text: item.videoTimeTxt}
    })
});
//TODO: templates/scenes.ftl:253 change scene view via ajax
export default connect(mapStateToProps,
    {fetchVideos, fetchRegister},
    null,
    {forwardRef: true})(LinceVideoPlayerList);

